<template>
  <div class="ts-body">
      <div class='search'>
          <a href="#" class="iconfont icon-wodexiaoxiicon"></a>
          <router-link class="write" tag="div" to='/main/search'>
              <i class="iconfont icon-sousuo2"></i>
              <input type="text" :placeholder="keyword" >
          </router-link>
          <a @click='shopping' class="iconfont icon-gouwuche2"></a>
      </div>
      <!-- <div class="sou" v-if="num">
          <Search :num.sync="num"></Search>
      </div> -->
  </div>
</template>
<script>
// import Search from './Search'
export default {
    // data(){
    //     return {
    //         num:false
    //     }
    // },
    // components:{
    //   Search
    // },
    // methods:{
    //     changeNum(){
    //         this.num=!this.num
    //     }
    // }
    computed:{
        keyword(){
            return this.$store.getters.Keyword
        }
    },
    methods:{
        shopping(){
            this.$router.push('/cart')
        }
    },
    mounted(){
        this.$store.dispatch('loadHotword');
    }
}
</script>
<style lang="less">
    .ts-body{
        padding: 0.1rem;
        position: fixed;
        width: 100%;
        top:0rem;
        height: 0.8rem;
        z-index: 9;
        background-color: white;
        border-bottom: 1px solid #eee;
        .search{
            // margin: 0.1rem;
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            align-items: center;
            text-align: center;
            
        }
        .iconfont,.write{
            display: inline-block;
        }
        .write{
            position: relative;
            border-radius: 0.1rem;
            text-align: left;
            line-height: 0.25rem;
            padding-left: 0.1rem;
            height: 0.55rem;
            width: 2.5rem;
            background-color: #eee;
            flex-grow: 6;
            .iconfont{
                font-size: 0.4rem;
            }
            input{
                width: 90%;
                height: 100%;
                position: absolute;
                background-color: transparent;
                border:none;
                outline: none;
                font-size: 0.22rem;
                color:#666;
                text-indent: 0.1rem;
            }
        }
        .iconfont{
            color: #666;
            text-decoration: none;
            font-size: 0.4rem;
            flex-grow: 1;
        }
    }
    @media screen and (min-width: 500px) {
        .ts-body{
            width: 600px!important;
        }
    }
    .sou{
        position: absolute;
        top: 0rem;
        bottom: 0rem;
        left: 0rem;
        right: 0rem;
        background-color: white;
        z-index: 999;
    }
</style>




