<template>
    <div class='rs-body'>
      <div class="p-fixed">
        <TopSearch></TopSearch>
        <div class="cm">
              <span v-for='(v,i) of pops' :key='i' :class='{act:iAct==i}' @click='changeSort(v.sortKey,i)'>{{v['title']}}</span>
         </div> 
      </div>
      <div class="likes wrapper" ref="wrapper">
        <div class="content">
          <a v-for='(v,i) of all' :key='i' @click="goDetails(v.img,v.price,v.title,v.tradeItemId)">
            <div class="shops-img">
                <img :src="v.img" alt="">
            </div>
            <div class="shops-title">
                <div class="tag">
                    <span v-for='(v1,i1) of v.props' :key='i1' v-if='i1<=6'>{{v1}}</span>
                </div>
                <div>
                    <span class="price">¥{{v.price}}</span>
                    <span class="favs">{{v.cfav}}<i class="iconfont icon-shoucang2"></i></span>
               </div>
            </div>
          </a>
          </div>
      </div>
    </div>
</template>
<script>
import Bscroll from "better-scroll";
import TopSearch from "../globalComponents/TopSearch";
export default {
  data() {
    return {
      q: null,
      iAct: false,
      fcid: null,
      title: null
    };
  },
  components: {
    TopSearch
  },
  computed: {
    all() {
      return this.$store.getters.GetAll;
    },
    pops() {
      return this.$store.getters.Pops;
    }
  },
  mounted() {
    this.q = this.$route.query.q;
    this.fcid = this.$route.query.fcid;
    this.title = this.$route.query.title;
    // console.log(this.fcid,this.title)
    if (this.q) {
      this.$store.dispatch("loadAll", this.q);
    }
    if (this.fcid && this.title) {
      this.$store.dispatch("loadPart", this.fcid, this.title);
    }
    this.$nextTick(() => {
      this.scroll = new Bscroll(this.$refs.wrapper, {
        click: true
      });
    });
  },
  methods: {
    changeSort(sk, i) {
      this.iAct = i;
      this.$store.commit("setPop", sk);
      if (this.q) {
        this.$store.dispatch("loadAll", this.q);
      }
      if (this.fcid && this.title) {
        this.$store.dispatch("loadPart", this.fcid, this.title);
      }
    },
    goDetails(img,price,title,itemId){
      this.$router.push('/details')
      
      var info={
        img:img,
        price:price,
        title:title,
        itemId:itemId
      }
      this.$store.commit('setInfo',info)
    }
  }
};
</script>
<style lang="less">
.rs-body {
  background-color: #eee;
  .p-fixed {
    position: fixed;
    background-color: white;
    height: 1.5rem;
    width: 100%;
    top: 0;
    z-index: 99;
    max-width: 600px;
    .cm {
      position: absolute;
      top: 0.8rem;
      height: 0.6rem;
      width: 100%;
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
      display: flex;
      align-items: center;
      span {
        text-align: center;
        display: block;
        width: 33%;
        height: 0.3rem;
        line-height: 0.3rem;
        font-size: 0.23rem;
        color: #333;
      }
      span:not(:last-child) {
        border-right: 1px solid #eee;
      }
    }
  }
  .act {
    color: #ef4562 !important;
  }
}
.rs-body .content {
  position: absolute;
  top: 1.5rem;
  bottom: 0rem;
  left: 0rem;
  right: 0rem;
  padding: 0 0.1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #eee;
  a {
    display: block;
    width: 49%;
    margin-top: 0.15rem;
    text-align: center;
    text-decoration: none;
    background-color: white;
    padding-bottom: 0.1rem;
    border-radius: 0.09rem;
    overflow: hidden;
    .shops-img {
      width: 100%;
      height: 3.6rem;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .shops-title {
      width: 100%;
      height: 1rem;
      padding: 0.1rem;
      text-align: left;
      font-size: 0.32rem;
      .tag {
        font-size: 0.2rem;
        span {
          display: inline-block;
          background-color: #eff3f6;
          color: #5a6f7a;
          padding: 0.02rem 0.03rem;
          margin: 0.03rem;
        }
      }
      .price {
        font-size: 0.28rem;
        color: black;
        font-weight: bold;
      }
      .favs {
        float: right;
        font-size: 0.2rem;
        line-height: 0.4rem;
        color: #666;
        font-weight: bold;
        padding-right: 0.13rem;
        i {
          color: rgb(96, 96, 96);
          margin-left: 0.1rem;
        }
      }
    }
  }
}
</style>



