<template>
   <div class="ct-body">
       <a href="#" class="banner" v-if='bannerhead.length'>
           <img :src='bannerhead[0].image' alt="">
       </a>
        <a class="ct" v-for='(v,i) of classifytop' :key='i' @click="goAll(v.link|fcid,v.title)">
            <div><img :src='v.image' alt=""></div>
            <p>{{v.title}}</p>
        </a>
        <div class="cm">
            <ClassifyMiddle :fcids='fcid'></ClassifyMiddle>
        </div>
   </div>
</template>
<script>
import ClassifyMiddle from './ClassifyMiddle'
import ClassifyBottom from './ClassifyBottom'
export default {
    data(){
        return{
            pid:null,
            fcid:null
        }
    },
    components:{
        ClassifyBottom,
        ClassifyMiddle
    },
    computed:{
        classifytop(){
            return this.$store.getters.Classifytop
        },
        bannerhead(){
            return this.$store.getters.GetBannerhead
        }
    },
    methods:{
        goAll(fcid,title){
            this.$router.push({path:'/result',query:{fcid,title}})
        }        
    },
    mounted(){
      this.pid=this.$route.params.pid;
      this.fcid=this.$route.params.fcid;
      this.$store.dispatch('loadClassifytop',this.pid);
    },
    beforeRouteUpdate(to,from,next){
        next();
        this.pid=this.$route.params.pid;
        this.fcid=this.$route.params.fcid;
        this.$store.dispatch('loadClassifytop',this.pid);
    },
    filiters:{
        fcid(link){
            var a=link.indexOf('=')
            var b=link.indexOf('&')
            return link.substring(a+1,b)
        }
    }
}
</script>
<style lang="less">
    .ct-body{
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        font-size: 0.16rem;
        text-align: center;
        .banner{
            display: block;
            width: 100%;
            height: 1.8rem;
            margin:0.1rem;
            img{
                width: 100%;
            }
        }
        .ct{
            display: block;
            text-decoration: none;
            width: 28.5%;
            height: 1.5rem;
            margin: 0.1rem;
            img{
                width: 90%;
                height: 1.2rem;
            }
            p{
                font-size: 0.16rem;
                color:#666;
            }
        }
        .cm{
            width: 100%;
        }
    }
</style>



