<template>
<div class="details">
    <i class="iconfont back icon-fanhui11" @click="goback"></i>
    <div class="wrapper" ref="wrapper">
        <div class="content">
            <div class="info">
                <!-- <i class="iconfont icon-fanhui11" @click="goback"></i> -->
                <div class="img">
                    <img :src='info.img' alt="">
                </div>
                <div class="describe">
                    <p>{{info.title}}</p>
                    <span><sup>¥</sup>{{info.price|currency}}</span>
                </div>
                <div class="promise">
                    <div>
                        <img src="../../../static/imgs/yes.png" alt="">
                        <span>72小时发货</span>
                    </div>
                    <div>
                        <img src="../../../static/imgs/yes.png" alt="">
                        <span>7天无理由退换</span>
                    </div>
                    <div>
                        <img src="../../../static/imgs/yes.png" alt="">
                        <span>退货补运费</span>
                    </div>
                </div>
            </div>
          
            <div class="comments">
                <div class="goto">
                    <span>评论</span>
                    <i class='iconfont icon-fanhui11' @click="goAllcomment"></i>
                </div>
                <div class="com"  v-for="(v,i) of comment" :key="i" v-if='i<=2'>
                    <div class="user">
                        <div class="avatar">
                             <img :src='v.user.avatar' alt="">
                        </div>
                       <span>{{v.user.uname}}</span>
                    </div>
                    <p>{{v.content}}</p>
                    <p class="time">{{v.time}}</p>
                </div>
            </div>
        </div>
    </div>
    <div class='addshops'>
        <div class="icon">
            <div class="sc"> 
                <div>
                    <li class="iconfont icon-guanzhu"></li>
                    <p>收藏</p>
                </div>
            </div>
        </div>
        <div class="icon">
            <div>
                <div>
                    <li class="iconfont icon-dianpu"></li>
                    <p>店铺</p>
                </div>
            </div>
        </div>
        <div class="btn"> 
        <li class="btn-add" @click="gocart">加入购物车</li>
        <li class="btn-buy">立即购买</li>
        </div>
    </div> 

    <div class="toast_div" style="width:4rem;height:3rem;top: 3rem;margin:0 auto;z-index: 99999; "></div>


</div>

</template>
<script>
import Bscroll from "better-scroll";
// import Comment from './Comment'
export default {
  computed: {
    info() {
      return this.$store.getters.Info;
    },
    comment() {
      return this.$store.getters.Comment;
    }
  },
  components: {
    Comment
  },
  watch: {
    info: function() {
      console.log(111);
    }
  },
  methods: {
    goAllcomment() {
      this.$router.push("/comment");
    },
    gocart() {
      
      $(".toast_div").toast({
        content: "添加成功",
        duration: 1000
      });
      // this.info.num=1;
      // console.log(this.info);

      this.$store.commit("setCart", this.info);
    },
    goback() {
      this.$router.back();
    }
  },
  mounted() {
    this.$store.dispatch("loadComment");
    console.log(1);

    this.$nextTick(() => {
        setTimeout(()=>{
            this.scroll = new Bscroll(this.$refs.wrapper, { click: true });
        },50)
      
    });
  }
};
</script>
<style lang="less">
.details {
  .back{
    display: inline-block;
    position: fixed;
    width: 0.6rem;
    height: 0.6rem;
    text-align: center;
    line-height: 0.6rem;
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 50%;
    top: 0.1rem;
    left: 0.1rem;
    font-size: 0.35rem;
    font-weight: bold;
    color: #ccc;
    z-index: 999;
  }
  .wrapper {
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0.9rem;
    background-color: #eaeaea;
    font-size: 0.3rem;
    color: #666;
    .content {
      width: 100%;
      .info {
        width: 100%;
        overflow: hidden;
        background-color: white;
        .img {
          width: 100%;
          height: 7rem;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .describe {
          padding: 0.2rem 0.3rem 0 0.3rem;
          color: #666;
          p {
            color: #424242;
            line-height: 0.3rem;
            font-size: 0.28rem;
          }
          sup {
            font-weight: blod;
            font-size: 0.2rem;
            color: black;
          }
          span {
            color: black;
            font-size: 0.6rem;
          }
        }
        .promise {
          width: 100%;
          height: 0.6rem;
          border-top: 1px solid #eee;
          border-bottom: 1px solid #eee;
          display: flex;
          align-items: center;
          justify-content: space-between;
          > div {
            width: 30%;
            font-size: 0.2rem;
            height: 0.3rem;
            text-align: center;
            img {
              width: 15%;
              vertical-align: middle;
            }
            span {
              vertical-align: middle;
            }
          }
        }
      }
      .comments {
        .goto {
          width: 100%;
          height: 0.5rem;
          background-color: white;
          margin-top: 0.1rem;
          padding: 0 0.3rem;
          line-height: 0.5rem;
          border-bottom: 1px solid #eee;
          span {
            font-size: 0.25rem;
          }
          i {
            float: right;
            transform: rotate(180deg);
            font-weight: bold;
          }
        }
        .com {
          background-color: white;
          padding: 0.1rem 0.3rem;
          .user {
            font-size: 0.24rem;
            padding-bottom: 0.1rem;
            .avatar {
              width: 0.6rem;
              height: 0.6rem;
              display: inline-block;
              border-radius: 50%;
              overflow: hidden;
              img {
                width: 100%;
              }
              vertical-align: middle;
            }
            span {
              font-size: 0.24rem;
              color: black;
              line-height: 0.6rem;
            }
          }
          p {
            font-size: 0.23rem;
            color: #727272;
          }
          .time {
            color: #999;
            font-size: 0.2rem;
            padding-left: 0.2rem;
          }
        }
      }
    }
  }
  .addshops {
    width: 100%;
    max-width: 600px;
    height: 0.9rem;
    background-color: white;
    position: fixed;
    bottom: 0;
    display: flex;
    color: #666;
    align-items: center;
    text-align: center;
    .icon {
      width: 1.2rem;
      height: 0.9rem;
      .sc {
        border-right: 1px solid #eee;
      }
      > div {
        display: flex;
        align-items: center;
        width: 1.2rem;
        height: 0.9rem;
        margin: 0 auto;
        > div {
          margin: 0 auto;
        }
      }
    }
    .iconfont {
      font-size: 0.3rem;
      margin-bottom: 0.02rem;
    }
    p {
      font-size: 0.2rem;
    }
    li {
      list-style: none;
      display: block;
    }
    .btn {
      display: flex;
      width: 100%;
      line-height: 0.9rem;
      font-size: 0.28rem;
    }
    .btn-add,
    .btn-buy {
      width: 50%;
      height: 0.9rem;
      text-align: center;
    }
    .btn-add {
      background-color: #ffe6e8;
      color: #ff5777;
    }
    .btn-buy {
      background-color: #ff5777;
      color: white;
      font-weight: bold;
    }
  }
}
</style>



