<template>
  <div class="ht-body">
      <a href="#" v-for='(v,i) of types' :key='i'>
          <div>
              <img :src='v.image' alt="">
          </div>
          <p>{{v.title}}</p>
      </a>
  </div>
</template>
<script>
import jsonp from 'fetch-jsonp'
export default {
  computed:{
      types(){
          return this.$store.getters.Types
      }
  },
  mounted(){
    this.$store.dispatch('loadTypes');
  }
}
</script>
<style lang="less">
    .ht-body{
        background-color: white;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        // margin-top: -0.3rem;
        padding:0.12rem 0.1rem;
        text-align: center;
        a{
            text-decoration: none;
            display: inline-block;
            // flex-grow: 3;
            width: 18%;
            height: 18%;
            margin-bottom: 0.1rem;
            // text-align: center;
            // border:1px solid #999;
        }
        div{
            width: 1rem;
            height: 1rem;
            margin: 0 auto;
        }
        img{
            width: 90%;
            height: 90%;
        }
        p{
            font-size: 0.2rem;
            color:rgb(96, 96, 96);
            margin-top: 0.1rem;
        }
    }
</style>





