<template>
<div class="cl-body">
    <div class="wrapper" ref="wrapper">
            <div class="content">
            <div v-for='(v,i) of list' :key='i'>
                <!-- <router-link> -->
                <router-link :to="'/classify/classifycontent/classtop/'+v.maitKey+'/'+v.miniWallkey" class="lis" tag="div">
                <p>{{v.title}}</p>
                </router-link>
            </div>
        </div>
    </div>
</div>
</template>
<script>
// https://mce.mogucdn.com/jsonp/multiget/3?appPlat=m&pids=41789?
import Bscroll from 'better-scroll'
import ClassifyTop from './ClassifyTop'
export default {
    components:{
      ClassifyTop  
    },
    computed:{
        list(){
            return this.$store.getters.List;
        }
    },
    mounted(){
        this.$nextTick(()=>{
            this.scroll = new Bscroll(this.$refs.wrapper, {
                click:true
            })
        })
        this.$store.dispatch('loadList');
    }
}
</script>
<style lang="less">
    .cl-body{
        width: 1.55rem;
        height: 100%;
        font-size: 0.25rem;
        text-align: center;
        color:#666;
        background-color: #f6f6f6;
        .lis{
            height: 0.7rem;
            p{
                display: inline-block;
                height: 0.3rem;
                margin-top:0.2rem;
                width: 100%;
            }
        }
        .wrapper{
            height: 100%;
        }
         .active{
            background-color: white;
            color:#FF5777!important;
            p{
                border-left: 2px solid #FF5777;
            }
        }
    }
</style>



