<template>
  <swiper :options="swiperOption" ref="mySwiper" class="hs">
    <!-- slides -->
    <swiper-slide v-for='(v,i) of wrapImgs' :key='i'>
        <img :src='v.image_800'>
    </swiper-slide>
    <!-- Optional controls -->
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import jsonp from 'fetch-jsonp'

export default {
  data() {
    return {
        swiperOption: {
          loop:true,
          autoplay:1500,
          pagination : '.swiper-pagination'
      }
    };
  },
  computed:{
        wrapImgs(){
            return this.$store.getters.wrapImgs
        }
  },
  components: {
    swiper,
    swiperSlide
  },
  mounted(){
      this.$store.dispatch('loadWrapImgs');
  }
};
</script>
<style>
    .hs{
        height: 2.9rem;
    }
    .hs img{
        width: 100%;
    }
    .swiper-pagination{
        position: relative;
        top:-1.2rem;
    }
</style>



