const getters={
    wrapImgs(state){
        return state.wrapImgs;
    },
    Types(state){
        return state.types
    },
    Beres(state){
        return state.beres
    },
    Hot(state){
        return state.hot
    },
    Likes(state){
        return state.likes
    },
    List(state){
        return state.list
    },
    Classifytop(state){
        return state.classifytop
    },
    GetBannerhead(state){
        return state.bannerhead
    },
    Sort(state){
        return state.sort
    },
    Sell(state){
        return state.sell
    },
    Hotword(state){
        return state.hotword
    },
    Keyword(state){
        return state.keyword
    },
    GetArr(state){
        return state.arr
    },
    GetAll(state){
        return state.all
    },
    Pops(state){
        return state.pops
    },
    // Part(data){
    //     return state.part
    // }
    Info(state){
        return state.info
    },
    Comment(state){
        return state.comment
    },
    Getcart(state){
        return state.cart
    }

}
export default getters;


