import fetchjsonp from 'fetch-jsonp'
const msc = "https://mce.mogucdn.com/jsonp/multiget/3";
const msc1="https://list.mogujie.com/search";
// 分类
const msc2="https://mce.mogucdn.com/jsonp/multiget/3";
const msc3="https://mce.mogujie.com/jsonp/makeup/3";
const msc4="https://list.mogujie.com/search";

// 关键字
const msc5="https://list.mogujie.com/module/mget";

//全网搜索
const msc6="https://list.mogujie.com/search"
// comment
const msc7="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1"

function parseParams(params){
    let paramsStr = "";
    for (const key in params) {
        paramsStr +=`${key}=${params[key]}&`
    }
    return paramsStr.substring(0,paramsStr.length-1);
}
const jsonp ={
    findDatasByPids(params){
        return fetchjsonp(msc+"?"+parseParams(params))
              .then((response)=>{
                 return response.json();
              });
     },
     findGuessLikes(params){
        return fetchjsonp(msc1+"?"+parseParams(params))
              .then((response)=>{
                 return response.json();
              });
     },
     findList(params){
        return fetchjsonp(msc2+"?"+parseParams(params))
        .then((response)=>{
           return response.json();
        });
     },
     findClasstop(params){
        return fetchjsonp(msc3+"?"+parseParams(params))
        .then((response)=>{
           return response.json();
        });
     },
     findClassSort(params){
         return fetchjsonp(msc4+"?"+parseParams(params))
         .then((response)=>{
            return response.json();
         });
     },


    //  hotword
    findHotword(params){
        return fetchjsonp(msc5+"?"+parseParams(params))
        .then((response)=>{
            return response.json()
        })
    },
    findallByq(params){
        return fetchjsonp(msc6+"?"+parseParams(params))
        .then((response)=>{
            return response.json()
        })
    },

    // 评论
    findComment(params){
        return fetchjsonp(msc7+"?"+parseParams(params))
        .then((response)=>{
            return response.json()
        })
    }
}
export default jsonp;


