<template>
  <div class="hh-body">
      <div class="title">
          <p>热门分类</p>
      </div>
      <div class="hots">
          <div class="htypes" v-for='(v,i) of hot' :key='i'>
              <img :src='v.image' alt="">
              <p>{{v.title}}</p>
          </div>
      </div>
  </div>
</template>
<script>
export default {
    computed:{
        hot(){
            console.log(this.$store.getters.Hot)
            return this.$store.getters.Hot
        }
    },
    mounted(){
        this.$store.dispatch('loadHot');
    }
};
</script>
<style lang="less">
.hh-body {
    background-color: white;
  .title {
    width: 60%;
    height: 0.6rem;
    margin: 0 auto;
    margin-top: 0.2rem;
    background: url("../../../static/imgs/1.png") 50% 50%no-repeat;
    background-size: 100%;
    text-align: center;
    p {
      font-size: 0.14rem;
      line-height: 0.6rem;
    }
  }
  .hots{
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      padding: 0.1rem;
      text-align: center;
      .htypes{
          width: 24%;
          height: 1.8rem;
        //   border:1px solid #999;
          margin-bottom: 0.1rem;
          img{
              width: 95%;
              height: 81%;
          }
          p{
              font-size: 0.18rem;
              line-height: 0rem;
              margin-top: -0.1rem;
              color:rgb(96, 96, 96);
          }
      }
  }
}
</style>



