const mutations={
    setWrapImgs(state,data){
        state.wrapImgs=data;
    },
    setTypes(state,data){
        state.types=data;
    },
    setBeres(state,data){
        state.beres=data
    },
    setHot(state,data){
        state.hot=data
    },
    setLikes(state,data){
        state.likes=data
    },
    setList(state,data){
        state.list=data
    },
    setClassifytop(state,data){
        state.classifytop=data
    },
    setbannerHead(state,data){
        state.bannerhead=data
    },
    setClassSort(state,data){
        state.sort=data
    },
    setSell(state,data){
        state.sell=data
    },
    setSort(state,data){
        state.sortkey=data
    },
    setHotword(state,data){
        state.hotword=data
    },
    setKeyword(state,data){
        state.keyword=data
    },
    setArr(state,data){
        state.arr.unshift(data);
        state.arr=Array.from(new Set(state.arr));
    },
    shan(state){
        // state.arr.length=0
        state.arr=[]
    },
    // 搜索
    setAll(state,data){
        state.all=data
    },
    setPops(state,data){
        state.pops=data
    },
    setPop(state,data){
        state.pop=data
    },
    setPart(state,data){
        state.part=data
    },
    // 存放宝贝info
    setInfo(state,data){
        state.info=data;
    },
    setComment(state,data){
        state.comment=data
    },

    //添加购物车
    setCart(state,data){
        // console.log(data);
        if(state.cart[data['itemId']]){
            state.cart[data['itemId']].num++;
            // state.cart[data['itemId']].num=state.cart[data['itemId']].num+1
        }else{
            // console.log(typeof data);
            if(typeof data!='object'){
                // return data
                state.cart[data['itemId']]=data
            }else{
                var newdata={}
                for(var attr in data){
                    newdata[attr]=data[attr];
                }
                newdata.num = 1;
                console.log(newdata);
                state.cart[data['itemId']]=newdata
            }
        }
        // state.cart.unshift(data)
        
    }
    
}
export default mutations;


