import Vue from 'vue'
import Router from 'vue-router'

import MainContent from '../components/simpleComponents/MainContent'
import HomeContent from '../components/simpleComponents/HomeContent'

import Mine from '../components/simpleComponents/Mine'

import Classify from '../components/simpleComponents/Classify'
import ClassifyContent from '../components/simpleComponents/ClassifyContent'
import ClassifyTop from '../components/simpleComponents/ClassifyTop'
import ClassifyBottom from '../components/simpleComponents/ClassifyBottom'

// 搜索框
import Search from '../components/globalComponents/Search'
import Result from '../components/simpleComponents/Result'


// 详情
import Details from '../components/simpleComponents/Details'
import Cart from '../components/simpleComponents/Cart'

//全部评论
import Comments from '../components/simpleComponents/Comment'
Vue.use(Router)

export default new Router({
  mode:'history',
  // linkExactActiveClass:"active",
  linkActiveClass:"active",
  routes: [


    {path:'/',redirect:"main"},
    {path: '/main',component:MainContent,children:[
      {path:'',redirect:"home"},
      {path:'home',component:HomeContent},
      {path:'search',component:Search}
      
      // {path:'classifycontent',component:ClassifyContent}
    ]},

    {path:'/result',component:Result},
    {path:'/comment',component:Comments},
    {path:'/classify',component:Classify,children:[
      {path:'',redirect:"classifycontent/classtop/41888/10062603"},
      {path:'classifycontent',component:ClassifyContent,children:[
        {path:'classtop/:pid/:fcid',component:ClassifyTop,children:[
          {path:'classmiddle',component:ClassifyBottom}
        ]}
      ]}
    ]},
    {path:'/details',component:Details},

    {path:'/cart',component:Cart},
    {path:'/mine',component:Mine}
  ]
})



