// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import TopSearch from './components/globalComponents/TopSearch'
import BottomNav from './components/globalComponents/BottomNav'
// import Vuex from 'Vuex'
import store from './store'
Vue.config.productionTip = false
// Vue.use(Vuex)
Vue.component('TopSearch', TopSearch)
Vue.component('BottomNav', BottomNav)
Vue.filter('currency', function (m) {
  if (m) {
    if (isNaN(m)) {
      var newm = m.substring(1);
      var n = newm.indexOf('.');
      if (n == -1) {
        return newm + ".00"
      }
      else {
        var c = newm.substring(n + 1)
        if (c.length == 1) {
          return newm + '0'
        }
        else {
          return newm + '00'
        }
      }
    } else {
      var m = m + ''
      var n = m.indexOf('.');
      if (n == -1) {
        return m + ".00"
      }
      else {
        var c = m.substring(n + 1)
        if (c.length == 1) {
          return m + '0'
        }
        if (c.length == 2) {
          return m
        }
        else {
          return m + '00'
        }
      }
    }
  }
  return "";


})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



