<template>
  <div class="hc-body wrapper" ref="wrapper">  
      <div class="hc content">
          <home-swiper></home-swiper>
          <HomeTypes></HomeTypes>
          <HomeBerefit></HomeBerefit>
          <HomeHot></HomeHot>
          <HomeGuess></HomeGuess>
      </div>
  </div>
</template>
<script>
import Bscroll from 'better-scroll'
import HomeSwiper from './HomeSwiper'
import HomeTypes from './HomeTypes'
import HomeBerefit from './HomeBerefit'
import HomeHot from './HomeHot'
import HomeGuess from './HomeGuess'
export default {
    components:{
        HomeSwiper,
        HomeTypes,
        HomeBerefit,
        HomeHot,
        HomeGuess
    },
  mounted(){
      this.$nextTick(()=>{
          this.scroll = new Bscroll(this.$refs.wrapper, {click:true})
      })
  }
}
</script>
<style lang="less">
    .hc-body{
        position: absolute;
        top: 0.8rem;
        bottom: 0.9rem;
        left: 0rem;
        right: 0rem;
    }
</style>



