<template>
  <div class="cm-body">
      <div class="cm">
            <span v-for='(v,i) of sort' :key='i' :class='{act:iAct==i}' @click='changeSort(v.sortKey,i)'>{{v['title']}}</span>
      </div>
        <div class="cb">
            <ClassifyBottom :fcids='fcids'></ClassifyBottom>
        </div>
  </div>
</template>
<script>
import ClassifyBottom from './ClassifyBottom'
export default {
    data(){
        return{
            iAct:false
        }
    },
    props:{
        fcids:null
    },
    watch:{
        fcids(newFcids){
            this.$store.dispatch('loadSell',newFcids)
        }
    },
    computed:{
        sort(){
            return this.$store.getters.Sort
        }
    },
    components:{
        ClassifyBottom
    },
    methods:{
        changeSort(sk,i){
            this.iAct=i;
            this.$store.commit('setSort',sk);
            this.$store.dispatch("loadSell");
        }
    }
}
</script>
<style lang="less">
    .cm-body{
        .cm{
            height: 0.6rem;
            width: 100%;
            border-top:1px solid #eee;
            border-bottom:1px solid #eee;            
            display: flex;
            align-items: center;
            span{
                display: block;
                width: 33%;
                height: 0.4rem;
                line-height: 0.4rem;
                font-size: 0.23rem; 
                color:#333;
            }
            span:not(:last-child){
                border-right: 1px solid #eee;
            }
        }
        .act{
            color:#ef4562!important;
        }
        .cb{
            width: 100%;
            // height: 4rem;
        }
    }
</style>



