<template>
  <div class="hg-body">
      <div class="title">
          <p>猜你喜欢</p>
      </div>
      <div class="likes">
          <a v-for='(v,i) of likes' :key='i' @click="goDetails(v.show.img,v.price,v.title,v.iid)">
            <div class="shops-img">
                <img :src="v.show.img" alt="">
            </div>
            <div class="shops-title">
               <p>{{v.title}}</p>
               <span class="price">{{v.price}}</span>
               <span class="favs">{{v.cfav}}<i class="iconfont icon-shoucang2"></i></span>
            </div>
          </a>
      </div>
  </div>
</template>
<script>
export default {
  computed:{
    likes(){
      return this.$store.getters.Likes;
    }
  },
  mounted(){
    this.$store.dispatch('loadLikes');
  },
  methods:{
    goDetails(img,price,title,itemId){
      this.$router.push('/details')
      
      var info={
        img:img,
        price:price,
        title:title,
        itemId:itemId
      }
      this.$store.commit('setInfo',info)
    }
  }
};
</script>
<style lang="less">
.hg-body {
    background-color: white;
  .title {
    width: 60%;
    height: 0.6rem;
    margin: 0 auto;
    margin-top: 0.2rem;
    background: url("../../../static/imgs/1.png") 50% 50%no-repeat;
    background-size: 100%;
    text-align: center;
    p {
      font-size: 0.14rem;
      line-height: 0.6rem;
    }
  }
  a{
    display: inline-block;
    width: 50%;
    // height: 4.6rem;
    padding: 0.1rem;
    text-align: center;
    text-decoration: none;
    .shops-img{
      width: 100%;
      height: 3.5rem;
      padding: 0 0.1rem;
      img{
        width: 100%;
        height: 100%;
      }
    }
    
    .shops-title{
      width: 100%;
      height: 1rem;
      padding-top: 0.1rem;
      text-align: left;
      font-size: 0.32rem;
      p{
        font-size: 0.15rem;
        text-indent: 0.2rem;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden; 
        color:rgb(96,96,96)
      }
      .price{
        font-size: 0.28rem;
        color:black;
        font-weight: bold;
      }
      .favs{
        float:right;
        font-size: 0.2rem;
        line-height: 0.4rem;
        color:#666;
        font-weight: bold;
        padding-right: 0.13rem;
        i{
          color:rgb(96,96,96);
          margin-left:0.1rem;
        }
      }
    }
  }
}
</style>




