import jsonp from '../api'

const actions = {

    loadWrapImgs({ commit, state }) {
        if (state.wrapImgs.length == 0) {
            jsonp.findDatasByPids({
                appPlat: "m",
                pids: "51822"
            }).then(({ data }) => {
                commit("setWrapImgs", data["51822"]["list"]);
            })
        }

    },
    loadTypes({ commit, state }) {
        if (state.types.length == 0) {
        jsonp.findDatasByPids({
            appPlat: "m",
            pids: "106930"
        }).then(({ data }) => {
            commit("setTypes", data["106930"]["list"]);
        })
    }
    },
    loadBeres({ commit, state }) {
        if (state.beres.length == 0) {
        jsonp.findDatasByPids({
            appPlat: "m",
            pids: "51833"
        }).then(({ data }) => {
            commit("setBeres", data["51833"]["list"]);
        })
    }
    },
    loadHot({ commit, state }) {
        if (state.hot.length == 0) {
        jsonp.findDatasByPids({
            appPlat: "m",
            pids: "51836"
        }).then(({ data }) => {
            commit("setHot", data["51836"]["list"]);
        })
    }
    },
    loadLikes({ commit, state }) {
        if (state.likes.length == 0) {
        jsonp.findGuessLikes({
            pid: "9750",
            sort: "pop"
        }).then(({ result }) => {
            commit("setLikes", result['wall']["list"]);
        })
    }
    },
    loadList({ commit, state }) {
        if (state.list.length == 0) {
        jsonp.findList({
            appPlat: 'm',
            pids: '41789'
        }).then(({ data }) => {
            commit("setList", data['41789']["list"]);
        })
        }
    },
    loadClassifytop({ commit, state }, pid) {
        jsonp.findClasstop({
            appPlat: 'm',
            pid: pid
        }).then(({ data }) => {
            commit("setClassifytop", data['categoryNavigation']["list"]);
            if(data['bannerHead']['list']){
                commit("setbannerHead",data['bannerHead']['list']);
            }
           
        })
    },
    loadClassifymiddle({ commit, state }, fcids) {
        console.log(fcids)
        jsonp.findClassSort({
            fcid: fcids,
            cKey: "h5-cube"
        }).then(({ result }) => {
            commit("setClassSort", result['sortFilter']);
        })
    },
    loadSell({ commit, state }, fcids) {
        jsonp.findClassSort({
            fcid: fcids,
            cKey: "h5-cube",
            page: 1,
            sort: state.sortkey
        }).then(({ result }) => {
            commit("setSell", result['wall']['docs']);
            commit("setClassSort", result['sortFilter']);
        })
    },


    // hotword
    loadHotword({commit,state}){
        if(state.hotword.length==0&&state.keyword.length==0){
            jsonp.findHotword({
                platform:'H5',
                code:'hotWord,sketch'
            }).then(({data})=>{
                commit('setHotword',data['hotWord']['data']);
                commit('setKeyword',data['sketch']['data']['frontword']);
            })
        }
        
    },

    loadAll({commit,state},q){
        jsonp.findallByq({
            q:q,
            cKey:"46",
            page:'1',
            sort:state.pop
        }).then(({result})=>{
            commit('setAll',result['wall']['docs']);
            commit('setPops',result['sortFilter']);
        })
    },
    loadPart({commit,state},fcid,title){
        jsonp.findallByq({
            fcid:fcid,
            cKey:"16",
            title:title,
            sort:state.pop,
            page:'1'
        }).then(({result})=>{
            commit('setAll',result['wall']['docs']);
            commit('setPops',result['sortFilter']);
            // commit('setPart',result['wall']['docs']);
        })
    },

    // comment
    loadComment({commit,state}){
        jsonp.findComment({
            type:"1",
            itemId:state.info.itemId,
            appPlat:"m"
        }).then(({data})=>{
            commit('setComment',data['list'])
        })
    }
}
export default actions


