<template>
  <div class="cart">
      <div class="cart-top">
          <i class="iconfont icon-fanhui11" @click="goback"></i>
          <span>购物车({{total}})</span>
          <span>编辑</span>
          <i class="iconfont icon-wodexiaoxiicon"></i>
      </div>
      <div class="carts wrapper" ref="wrapper">
        <div class="content">
          <div class="goods" v-for="(v,i) of cart" :key="i">
            <div class="check">
              <input type="checkbox" v-model='v.checked' @change='checkall' @click="allmoney(v.num,v.price)">
            </div>
            <div class="good" @click="godetails(v.img,v.price,v.itemId,v.title)">
              <div class="imgs">
                <img :src="v.img" alt="">
              </div>
              <div class="goodinfo">
                  <p>{{v.title}}</p>
                  <p>¥{{v.price|currency}} <span>{{v.num}}件</span></p>
                  
              </div>
            </div>
          </div>
        </div>
       
      </div> 
      <div class="cart-bottom">
        <div>
          <input type="checkbox" id='select' v-model="all" @change="selectall"><label for="select">全选({{num}})</label>
          <span>{{moneny}}</span>
        </div>
          <button>去结算</button>
      </div>
  </div>
</template>
<script>
import Bscroll from "better-scroll";
export default {
  data(){
    return{
      all:false,
      num:0,
      moneny:'0.00'
    }
  },
  computed:{
    cart(){
      var cart=this.$store.getters.Getcart
      cart=Object.values(cart)
      cart=cart.reverse()
      return cart;
    },
    total(){
      var total=Object.keys(this.cart)
      return total.length
    }
  },
  methods:{
    goback(){
      this.$router.back()
    },
    selectall(){
      this.num=0;
      var cart=Object.values(this.cart)
      cart.forEach(v => {
        v.checked=this.all
      })
      if(this.all){
        this.num=this.cart.length;
      }else{
        this.num=0;
      }
    },
    checkall(){
      this.num=0;
      this.all=true;
      var cart=Object.values(this.cart)
      for (let i = 0; i < cart.length; i++) {
          if(cart[i].checked){
            console.log(cart[i].checked)
              this.num++;
              continue;
          }
          this.all = cart[i].checked;
      }
    },
    godetails(img,price,itemId,title){
      var info={
        img:img,
        price:price,
        itemId:itemId,
        title:title
      }
      // console.log(info)
      this.$router.push({path:'/details',query:info})
      this.$store.commit('setInfo',info)
    },
    allmoney(m,p){
      var sum=0;
      console.log(p,'---')
        if(isNaN(p)){
          p=p.substring(1)*1
        }
         sum=sum+p
         sum=sum+''
         this.moneny=sum
    }

  },
  mounted(){
    this.$nextTick(() => {
      this.scroll = new Bscroll(this.$refs.wrapper, { click: true });
    });
  }
}
</script>
<style lang="less">
    .cart{
      background-color: #f6f6f6;
      position: absolute;
      width: 100%;
      height: 100%;
      .cart-top{
        max-width: 600px;
        width: 100%;
        height: 0.8rem;
        position: fixed;
        top:0;
        background-color: #fafafa;
        border-bottom:1px solid #c9c7c8;
        display: flex;
        justify-content: space-around;
        padding: 0.1rem 0.2rem;
        text-align: center;
        line-height: 0.6rem;
        z-index: 99;
        *{
          display: block;
           color:#666;
        }
        i{
          font-size: 0.35rem;
        }
        span:nth-child(2){
        text-align: center;
          font-size: 0.35rem;
          width: 70%;
          margin: 0 auto;
        }
        span:nth-child(3){
          font-size: 0.24rem;
          padding-right: 0.1rem;
        }
      }
      .carts{
        position: absolute;
        top:0.8rem;
        bottom:0.9rem;
        width: 100%;
        z-index: 9;
        .content{
          width: 100%;
          .goods{
            width: 100%;
            display: flex;
            align-items: center;
            border-bottom:1px solid #d8d8d8;
            padding:0.1rem 0.3rem;
            font-size: 0.2rem;
            .check{
              width: 0.3rem;
              height: 0.3rem;
              margin: 0.2rem;
              display: inline-block;
            }
            .good{
              width: 100%;
              height: 100%;
              display: flex;
              .imgs{
                display: inline-block;
                width: 1rem;
                // height: 100%;
                height: 1.4rem;
                margin-right:0.1rem;
                overflow: hidden;
                img{
                  width: 100%;
                  height: 100%;
                }
              }
              .goodinfo{
                padding:0.1rem 0;
                width: 4rem;
                p:first-child{
                  color:#5e5e5e;
                  padding: 0.1rem;
                }
                p:last-child{
                  color:#333;
                  line-height: 0.5rem;
                  padding-left: 0.2rem;
                  span{
                    margin-left: 0.5rem;
                  }
                }
              }
            }
          }
        }
      }
      .cart-bottom{
        width: 100%;
        max-width: 600px;
        height: 0.9rem;
        border-top:1px solid #d8d8d8;
        background-color: #fefefe;
        position: fixed;
        bottom:0;
        z-index: 99;
        padding: 0.1rem 0.35rem;
        font-size: 0.25rem;
        line-height: 0.7rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        label{
          font-size: 0.27rem;
          color:#666;
        }
        span{
          color: #ff5777;
          font-size: 0.3rem;
          // line-height: 0.7rem;
          font-weight: bold;
          margin-left: 0.2rem;
        }
        button{
          width: 1.5rem;
          height: 0.6rem;
          color:white;
          background-color: #ff5777;
          outline: none;
          border-radius: 0.1rem;
          border:none;
          font-size: 0.25rem;
          font-weight: bold;
        }
      }
      input{
          -webkit-appearance: none;
          width: 0.3rem;
          height: 0.35rem;
          border:none;
          outline: none;
          background: url('../../../static/imgs/checkbox.png')no-repeat;
          background-position: 0rem -0.35rem;
          background-size: 100%;
          vertical-align: middle;
          margin-right: 0.1rem;
        }
      input:checked{
          background-position: 0rem 0rem;
      }
    }
</style>




