<template>
   <div class="search-body">
       <div class="top">
           <a @click="goback" class="iconfont icon-fanhui11"></a>
           <input type="text" :placeholder="keyword" autofocus="autofocus" v-model="msg">
           <a @click='add'>搜索</a>
       </div>
       <div class="hotsearch">
           <div class="history">
               <i class="iconfont icon-sousuo2"><span>历史搜索</span></i>
              
               <i class="iconfont icon-shanchu" @click="shan"></i>
                <p v-if='!arr.length'>暂无历史记录</p>
           </div>
           <div class="tips">
               <span v-for="(v,i) of arr" :key="i" @click="jilu">{{v}}</span>
           </div>
           <div class="rmss">
               <i class="iconfont icon-shoucang2"><span>热门搜索</span></i>
           </div>
           <div class="tips">
               <span v-for='(v,i) of hotword' :key='i' :style="'color:'+v.color" @click='jilu'>{{v.frontword}}</span>
           </div>
       </div>
   </div>
</template>
<script>

export default {
    data(){
        return{
            msg:'',
            shanchu:[]
           
        }
    },
    computed:{
        hotword(){
            return this.$store.getters.Hotword;
        },
        keyword(){
            return this.$store.getters.Keyword
        },
        arr(){
            return this.$store.getters.GetArr;
        }
    },
    methods:{
        goback(){
            this.$router.back();
        },
        add(){
          if(!this.msg){
              this.msg=this.$store.getters.Keyword;
          }  
          this.$store.commit('setArr',this.msg);  
          this.$router.push({path:'/result',query:{q:this.msg}});
        },
        shan(){
            this.$store.commit('shan');
        },
        jilu(e){
            this.msg=e.target.innerText;
            this.$store.commit('setArr',this.msg);  
            this.$router.push({path:'/result',query:{q:this.msg}});
        }
    },
    // watch:{
    //     arr(newarr){
    //         return this.$store.getters.GetArr;
    //     }
    // },
    mounted(){
          this.$store.dispatch('loadHotword');
    }
}
</script>
<style lang="less">
    .search-body{
        width: 100%;
        height: 100%;
        background-color: white;
        position:absolute;
        top:0;
        left: 0;
        z-index: 99;
        font-size: 0.28rem;
        color:#666;
        .history{
            width: 100%;
            .icon-shanchu{
                float: right;
                margin-right: 0.2rem;;
            }
            p{
                font-size: 0.25rem;
                line-height: 0.6rem;
                padding-left: 0.5rem;
            }
            padding:0 0.2rem;
            padding-top:0.1rem;
        }
        .top{
            display: flex;
            align-items: center;
            padding: 0 0.1rem;
            text-align: center;
            a{
                display: block;
                width: 1rem;
                text-decoration: none;
                flex-grow: 1;
                color:#666;
            }
            a:first-child{
                width: 0.8rem;
                font-size: 0.4rem;
            }
            input{
                flex-grow: 8;
                width: 93%;
                border:1px solid #666;
                height: 0.4rem;
                border-radius: 0.1rem;
                text-indent: 0.2rem;
                outline:none;

            }
            input:focus{
                 border:1px solid #ff4466;
            }
            position: absolute;
            top:0;
            height: 0;
            width: 100%;
            height: 0.8rem;
            border-bottom:1px solid #eee;
        }
       .rmss{
            padding:0.1rem 0.2rem;
        }
        .rmss{
            border-top:1px solid #eee;
        }
        .hotsearch{
            position: absolute;
            top:0.8rem;
            i{
                font-size: 0.26rem;
                span{
                    padding: 0 0.1rem;
                }
            }
            .tips{
                width: 100%;
                margin: 0.1rem;
                padding: 0 0.2rem;
                span{
                    display: inline-block;
                    padding: 0.1rem;
                    height: 0.45rem;
                    border:1px solid #eee;
                    font-size: 0.2rem;
                    line-height: 0.25rem;
                    border-radius: 0.1rem;
                    margin: 0.05rem 0.1rem;
                }
            }
        }
    }
</style>



