<template>
    <div class="cb-bottom">
        <!-- <div class="sell" v-for="(v,i) of sell" :key='i'></div> -->
        <div class="sell">
          <a v-for='(v,i) of sell' :key='i' @click="goDetails(v.img,v.price,v.title,v.iid)">
            <div class="shops-img">
                <img :src="v.img" alt="">
                <div class="sold">已售{{v.sale}}</div>
            </div>
            <div class="shops-title">
               <p>{{v.title}}</p>
               <span class="price">¥{{v.price}}</span>
               <span class="favs">{{v.cfav}}<i class="iconfont icon-shoucang2"></i></span>
            </div>
          </a>
      </div>
    </div>
</template>
<script>
export default {
  props: {
    fcids: null
  },
  computed: {
    sell() {
      return this.$store.getters.Sell;
    }
  },
  methods:{
    goDetails(img,price,title,itemId){
      this.$router.push('/details')
      
      var info={
        img:img,
        price:price,
        title:title,
        itemId:itemId
      }
      this.$store.commit('setInfo',info)
    }
  },
  mounted() {
    this.$store.dispatch("loadSell", this.fcids);
  },
  beforeRouteUpdate(to, from, next) {
    next();
    this.$store.dispatch("loadSell", this.fcids);
  }
};
</script>
<style lang="less">
.cb-bottom {
  a {
    display: inline-block;
    width: 50%;
    padding-top: 0.1rem;
    text-align: center;
    text-decoration: none;
    .shops-img {
      width: 100%;
      height: 3.5rem;
      padding: 0 0.1rem;
      font-size: 0.2rem;
      position: relative;
      img {
        width: 100%;
        height: 100%;
      }
      .sold {
        position: absolute;
        bottom: 0.3rem;
        width: 1.5rem;
        height: 0.4rem;
        //   background-color: rgba(0,0,0,0.4);
        background: -webkit-linear-gradient(left top, #ace, #f96);
        color: white;
        font-weight: bold;
        line-height: 0.4rem;
      }
    }
    .shops-title {
      width: 100%;
      height: 1rem;
      padding-top: 0.1rem;
      text-align: left;
      font-size: 0.32rem;
      p {
        font-size: 0.15rem;
        text-indent: 0.2rem;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        color: rgb(96, 96, 96);
      }
      .price {
        font-size: 0.28rem;
        color: #ef4562;
        font-weight: bold;
      }
      .favs {
        float: right;
        font-size: 0.2rem;
        line-height: 0.4rem;
        color: #666;
        font-weight: bold;
        padding-right: 0.13rem;
        i {
          color: rgb(96, 96, 96);
          margin-left: 0.1rem;
        }
      }
    }
  }
}
</style>



