<template>
<div class="all-body">

    <div class="all">
            <i class="iconfont icon-fanhui11" @click='goback'></i>
            <span>所有评论</span>
            <i class="iconfont icon-gouwuche2" @click='gocart'></i>
    </div>
  <div class="comment wrapper" ref="wrapper">
      <div class="content">
          <div class="c" v-for="(v,i) of comment" :key="i">
              <div class="nick">
                  <img :src='v.user.avatar' alt="">
              </div>
              <div class="say">
                  <p>{{v.user.uname}}</p>
                  <p>{{v.content}}</p>
                  <span>{{v.time}}</span>
                  <span>{{v.style}}</span>
                 <p><span v-for='(ve,ie) of v.extraInfo' :key='ie'>{{ve}}</span></p>
                 <div class="show" v-if='v.images.length'>
                     <div v-for='(v1,i1) of v.images' :key='i1' class="forimg">
                        <img  :src='v1' alt="">
                     </div> 
                 </div>  
              </div>
          </div>
      </div>
  </div>
</div>
</template>
<script>
import Bscroll from "better-scroll";
export default {
  computed: {
    comment() {
      return this.$store.getters.Comment;
    }
  },
  methods:{
      goback(){
          this.$router.back()
      },
      gocart(){
          this.$router.push('/cart')
      }
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new Bscroll(this.$refs.wrapper, { click: true });
    });
  }
};
</script>
<style lang="less">
.all-body {
  .all {
    width: 100%;
    height: 0.7rem;
    position: fixed;
    top: 0;
    z-index: 99;
    background-color: #fafafa;
    border-bottom: 1px solid #c9c7c8;
    line-height: 0.7rem;
    display: flex;
    text-align: center;
    i,
    span {
      display: inline-block;
    }
    i {
      width: 1rem;
      font-size: 0.35rem;
    }
    span {
      flex-grow: 1;
      font-size: 0.35rem;
    }
  }
  .comment {
    position: absolute;
    top: 0.7rem;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f6f6f6;
    font-size: 0.3rem;
    color: #5e5e5e;
    .content{
        padding: 0.1rem 0.2rem;
        background-color: white;
        .c{
            width: 100%;
            padding: 0.2rem;
            border-bottom: 1px solid #e5e5e5;
            display: flex;
            .nick{
                width: 0.8rem;
                height: 0.8rem;
                overflow: hidden;
                box-shadow: 0px 0px 3px #ccc;
                img{
                    width: 100%;
                }
            }
            .say{
                width: 80%;
                padding-left: 0.3rem;
                p{
                    font-size:0.22rem;
                    color:#424242;
                }
                p:first-child{
                    color:black;
                    margin-bottom: 0.1rem;
                }
                span{
                    font-size: 0.2rem;
                    padding:0 0.05rem;
                }
                .show{
                    // width: 100%;
                    // position: relative;
                    height: 3rem;
                    overflow-y: hidden;
                    overflow-x:scroll;
                    // display: flex; 
                    white-space: nowrap; 
                    .forimg{
                        width: 2rem;
                        height: 3rem;
                        margin-right: 0.1rem;
                        display: inline-block;
                        
                        img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
            }
        }
    }
  }
}
</style>



