<template>
  <div class="hb-body">
      <div class="title">
          <p>超实惠－促销直达</p>
      </div>
      <div class="bere">
          <div v-for='(v,i) of beres' :key='i'>
              <div class="vtitle">
                <p>{{v.title}}</p>
                <p>{{v.viceTitle}}</p>
              </div>
              <img :src='v.image' alt="">
          </div>
      </div>
  </div>
</template>
<script>

export default {
  computed:{
    beres(){
      return this.$store.getters.Beres;
    }
  },
  mounted() {
    this.$store.dispatch('loadBeres');
  }
};
</script>
<style lang="less">
.hb-body {
  background-color: white;
  overflow: hidden;
  .vtitle {
    p {
      font-size: 0.2rem;
    }
    p:last-child {
      color: #ff5f7e;
      font-size: 0.16rem;
      line-height: 0.4rem;
      font-weight: bold;
    }
  }

  // padding-top: 0.2rem;
  .title {
    width: 60%;
    height: 0.6rem;
    margin: 0 auto;
    margin-top: 0.2rem;
    background: url("../../../static/imgs/1.png") 50% 50%no-repeat;
    background-size: 100%;
    text-align: center;
    p {
      font-size: 0.14rem;
      line-height: 0.6rem;
    }
  }
  .bere {
    // display: flex;
    // float: right;
    // flex-wrap: wrap;
    display: inline;
    img{
        width: 1.8rem;
    }
  }
  .bere > div {
    text-align: center;
    width: 50%;
    height: 3rem;
    background-color: white;
    border-top: 1px solid #E5E5E5;
    padding-top: 0.1rem;
    overflow: hidden;
  }
  .bere>div:nth-child(1){
      float:left;      
      width: 55%;
      border-right: 1px solid #E5E5E5;
      
  }
  .bere>div:nth-child(2){
      width: 45%;
      height: 1.5rem;
      float:right;
      img{
        width: 1rem;
    }
  }
  .bere>div:nth-child(3){
      width: 45%;
      height: 1.5rem;
      float:right;
      
      img{
        width: 1rem;
    }
  }
   .bere>div:nth-child(4){
      width: 33.33%;
      height: 1.8rem;
      float:left;
      border-right: 1px solid #E5E5E5;
      img{
        width: 1rem;
    }
  }
   .bere>div:nth-child(5){
      width: 33.33%;
      height: 1.8rem;
      float:left;
      border-right: 1px solid #E5E5E5;
      img{
        width: 1rem;
    }
  }
   .bere>div:nth-child(6){
      width: 33.33%;
      height: 1.8rem;
      float:left;
      img{
        width: 1rem;
    }
  }

}
</style>




