<template>
  <div class="mine wrapper" ref="wrapper">
      <div class="content">
        <div class="user">
          <div class="head">
            <img src="../../../static/imgs/2.jpg" alt="">
          </div>
          <div class="nick">
            <p>列那狐偷鱼</p>
            <p>我的收货地址<i class="iconfont icon-fanhui11"></i></p>
          </div>
        </div>

        <div class="item dd">
            <p class="imgs"><img src="https://s10.mogucdn.com/p1/160727/upload_ie4wenzume4tsmjqmezdambqgqyde_56x56.png"></p>
            <div class="text">
              <p>我的订单</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
        </div>
        <div class="my">
           <div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/p2/161130/159_0ihj4c5a23kabh84ka8dfhl552dla_56x56.png"></p>
            <div class="text">
              <p>我的购物车</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
           </div><div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/mlcdn/c45406/170419_7c5a4j17k0061bg4kiagf9lc69fe8_56x56.png"></p>
            <div class="text">
              <p>我的拼团</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
           </div><div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/mlcdn/c45406/170803_840decficafea4lfg10gjc2a020gc_56x56.png"></p>
            <div class="text">
              <p>我的钱包</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
           </div><div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/p2/160822/159_00ijba9e46e13970ec7kakll8a06a_56x56.png"></p>
            <div class="text">
              <p>我的优惠券</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
           </div><div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/p1/160727/upload_ifqtsndeg43gkmrqmezdambqgyyde_56x56.png"></p>
            <div class="text">
              <p>我收藏的商品</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
           </div><div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/p1/160727/upload_ie4wmy3gmq3wkmrqmezdambqgqyde_56x56.png"></p>
            <div class="text">
              <p>我收藏的店铺</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
           </div>
        </div>



        <div class="msg">
          <div class="item">
            <p class="imgs"><img src="https://s10.mogucdn.com/p1/160727/upload_ie4wenzume4tsmjqmezdambqgqyde_56x56.png"></p>
            <div class="text">
              <p>我收藏的店铺</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
          </div>
           <div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/p1/160727/upload_ie4tgm3ege4wkmrqmezdambqgqyde_56x56.png"></p>
            <div class="text">
              <p>消息通知</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
          </div>
          <div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/p1/160307/idid_ifrtqmdfgztggnzsg4zdambqhayde_84x84.png"></p>
            <div class="text">
              <p>客服</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
          </div>
          <div class="item">
            <p class="imgs"><img src="//s10.mogucdn.com/p1/160727/upload_ifrdonbugezgkmrqmezdambqmeyde_56x56.png"></p>
            <div class="text">
              <p>意见反馈</p>
              <i class="iconfont icon-fanhui11"></i>
            </div>
          </div>
        </div>

        <div class="exit">退出登录</div>
      </div>
  </div>
</template>
<script>
import Bscroll from "better-scroll";
export default {
  mounted() {
    this.$nextTick(() => {
      this.scroll = new Bscroll(this.$refs.wrapper, { click: true });
    });
  }
};
</script>
<style lang="less">
.mine {
  font-size: 0.2rem;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #f6f6f6;
  .content {
    width: 100%;
    .user {
      background-color: white;
      width: 100%;
      height: 1.7rem;
      display: flex;
      padding: 0.2rem 0.4rem;
      justify-content: space-between;
      align-items: center;
      .head {
        width: 1rem;
        height: 1rem;
        border-radius: 50%;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .nick {
        width: 5rem;
        height: 1rem;
        padding: 0.1rem 0.2rem;
        color: #333;
        p:first-child {
          font-size: 0.3rem;
        }
        p:last-child {
          font-size: 0.22rem;
          line-height: 0.5rem;
          i{
            transform: rotate(180deg)!important;
            color: #666;
            font-size: 0.26rem;
            font-weight: bold;
            padding-left: 0.5rem;
          }
        }
      }
    }
    .dd {
      margin-top: 0.2rem;
    }
    .item {
      width: 100%;
      height: 0.9rem;
      background-color: white;
      padding-left: 0.2rem;
      display: flex;
      align-items: center;
      .imgs {
        display: inline-block;
        width: 10%;
        height: 0.5rem;
        img {
          width: 0.5rem;
          height: 0.5rem;
        }
      }
      .text {
        width: 90%;
        height: 0.9rem;
        padding-left: 0.3rem;
        p {
          display: inline-block;
          line-height: 0.9rem;
          font-size: 0.28rem;
        }
        i {
          transform: rotate(180deg);
          font-size: 0.3rem;
          float: right;
          line-height: 0.8rem;
          margin-right: 0.3rem;
          //  overflow: hidden;
        }
      }
    }
    .my,
    .msg {
      margin-top: 0.2rem;
      > div:last-child .text{
        border-bottom:none;
      }
      .text {
        border-bottom: 1px solid #e5e5e5;
        
      }
    }
    .exit{
      width: 80%;
      height: 0.7rem;
      border-radius: 0.1rem;
      background-color: #ff5777;
      margin: 0 auto;
      margin-top: 0.2rem;
      color:white;
      text-align: center;
      font-size: 0.3rem;
      line-height: 0.7rem;
    }
  }
}
</style>



