<template>
  <div class="cc">
      <ClassifyList></ClassifyList>
      <div class="right wrapper" ref="wrapper">
        <div class="content">
            <router-view></router-view>
      </div>         
      </div>          
  </div>
</template>
<script>
import Bscroll from 'better-scroll'
import ClassifyList from "./ClassifyList";
export default {
  components: {
    ClassifyList
  },
   mounted(){
      this.$nextTick(()=>{
          this.scroll = new Bscroll(this.$refs.wrapper, {
            click:true
          })
      })
      this.$store.dispatch('loadList');
  }
};
</script>
<style lang="less">
.cc {
  position: absolute;
  top: 0.8rem;
  bottom: 0.9rem;
  left: 0rem;
  right: 0rem;
  background-color: white;
  display: flex;
  .right {
    background-color:white;
    width: 100%;
    height: 100%;
    position: relative;
 
  }
}
</style>



